import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import moment from 'moment';
import { IOptionsPSComplexDateFilter } from '../../commonBussinessSRC/psComponents/ps-complex-components/ps-complex-date-filter/ps-complex-date-filter.component.interfaces';
import { PsComplexWidgetDropdownsComponent } from '../../commonBussinessSRC/psComponents/ps-complex-components/ps-complex-widget-dropdowns/ps-complex-widget-dropdowns.component';
import { IOptionsPsComplexWidgetDropdownsExposed } from '../../commonBussinessSRC/psComponents/ps-complex-components/ps-complex-widget-dropdowns/ps-complex-widget-dropdowns.component.interface';
import { IOptionsPsDocumentsListComponent } from '../../commonBussinessSRC/psComponents/ps-documents-list/ps-documents-list.component.interface';
import { IOmniDocumentsReportObject, IOmniDocumentsRequest } from '../../commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniCommonService } from '../../commonBussinessSRC/psServices/omni-common/omni-common.service';
import { OmniPullService } from '../../commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsContainerLookupOptionComponent } from '../../commonSRC/psComponents/ps-base/ps-base-container/ps-container-lookup-option/ps-container-lookup-option.component';
import { ConstantCommon } from '../../commonSRC/psServices/models/common-constant';
import { PsSelect } from '../../commonSRC/psServices/models/common-type';
import { IFileFormat, IOptionsPsActionButton, IOptionsPsButtonReset, IOptionsPsContainerLookupOptionComponentExposed, IOptionsPsContainerPanel } from '../../commonSRC/psServices/models/ps-common-interface';
import { OmniBasePage } from '../omni-base/omni-base.page';
import { CommonUtils } from './../../commonSRC/psServices/models/common-utils';


@Component({
  selector: 'documents',
  templateUrl: './documents.page.html',
  styleUrls: ['./documents.page.scss'],
})
export class DocumentsPage extends OmniBasePage implements OnInit {

  private formGroup = new FormGroup({});
  public documentsPageData: any = {};
  itemCard: any;
  parentDocuments: any;
  cardOptions: IOptionsPsContainerLookupOptionComponentExposed;
  @ViewChild('productDetails', { static: true }) lookup: PsContainerLookupOptionComponent;
  @ViewChild('documentsDropdownWidget', { static: true }) documentsComplexWidget: PsComplexWidgetDropdownsComponent;
  panelOptions1: IOptionsPsContainerPanel = {
    isExpandable: true,
    iconName: 'document',
    expanded: true
  };
  panelOptions2: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'documents_filter_key',
    iconName: 'document',
    expanded: true
  };

  panelOptions3: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'documents_key',
    iconName: 'document',
    expanded: true,
  };

  filterDate: IOptionsPSComplexDateFilter = {};

  resetOptions: IOptionsPsButtonReset = {
    labelKey: 'reset_key',
    group: this.formGroup
  };

  filterOptions: IOptionsPsActionButton = {
    labelKey: 'filter_key',
    group: this.formGroup,
  }

  widgetDropdownsOptions: IOptionsPsComplexWidgetDropdownsExposed = {
    group: this.formGroup,
    requestObject: this.documentsPageData,
    dropdowns: [
      {
        group: this.formGroup,
        fcName: 'documentsDropdown',
        enablePagination: false,
        requestObject: this.documentsPageData
      },
    ]
  };
  documentListOptions: IOptionsPsDocumentsListComponent = {
    itemList: []
  };

  constructor(private omniPull: OmniPullService, private omniCommonService: OmniCommonService) {
    super();
  }

  ngOnInit() {
    this.baseFormGroup = this.formGroup;
    const param = this.omniPull.omniCommon.session.getValueOf(ConstantCommon.preLoginResponse).parameters;
    const defaultFormat = param.DefaultDateFormat ? param.DefaultDateFormat.parameterVal : 'DD/MM/YYYY';
    this.filterDate = {
      fromDate: {
        group: this.formGroup,
        fcName: 'fromDate',
        displayFormat: defaultFormat,
        placeHolder: 'from_date_key',
      },
      toDate: {
        group: this.formGroup,
        fcName: 'toDate',
        displayFormat: defaultFormat,
        placeHolder: 'to_date_key',
      }
    };
    this.lookup.showDocuments = false;
    this.cardOptions = this.navigationServices.getParamKey('options');
    this.cardOptions.isEditable = false;
    this.cardOptions.actionDetailsOptions = null;
    this.cardOptions.listOfStatementOptions = null;
    this.itemCard = this.navigationServices.getParamKey('itemCard');
    let fromOperName: string = this.navigationServices.getParamKey('fromOperName');
    let panel1Name: string = fromOperName;
    if (panel1Name.startsWith("My")) {
      panel1Name = panel1Name.replace("My ", "");
    }
    panel1Name = panel1Name.replace(" Documents", "");
    if (fromOperName.startsWith("My") && panel1Name.endsWith('s')) {
      panel1Name = panel1Name.replace("s", "");
    }
    this.panelOptions1.labelKey = panel1Name + " Details";
    this.parentDocuments = this.getParentDocumentsList();
  }
  fromDateCtrlFormat: any;
  toDateCtrlFormat: any;
  ionViewDidEnter(): void {
    super.ionViewDidEnter();
    //moved here by ghada, AZDB-4624 to eliminate repetition of getting date controls format
    this.fromDateCtrlFormat = this.formGroup.controls[this.filterDate.fromDate.fcName][ConstantCommon.CONTROLLER_DATE_FORMAT];
    this.toDateCtrlFormat = this.formGroup.controls[this.filterDate.toDate.fcName][ConstantCommon.CONTROLLER_DATE_FORMAT];
}

  resetFilter() {
    this.documentsComplexWidget.resetSelections();
    this.formGroup.get(this.filterDate.fromDate.fcName).setValue("");
    this.formGroup.get(this.filterDate.toDate.fcName).setValue("");
    this.formGroup.controls[this.filterDate.fromDate.fcName].markAsTouched();
    this.formGroup.controls[this.filterDate.toDate.fcName].markAsTouched();
  }

  getParentDocumentsList() {
    const parentOper = this.navigationServices.getParamKey('fromOper');
    const listOptions: PsSelect = this.omniCommonService.getParentDocumentsList(parentOper);
    this.widgetDropdownsOptions.dropdowns[0].listOfOptions = listOptions;
  }

  filterDocuments() {
    const filteredDocs = this.documentsComplexWidget.selectedItemsList;
    const fromDate = moment(this.formGroup.get(this.filterDate.fromDate.fcName).value).format(this.fromDateCtrlFormat);
    const toDate = moment(this.formGroup.get(this.filterDate.toDate.fcName).value).format(this.toDateCtrlFormat);
    if (this.formGroup.valid) {
      if (filteredDocs.length > 0) {
        this.documentListOptions.fromDate = fromDate;
        this.documentListOptions.toDate = toDate;
        const operIdsList: number[] = [];
        filteredDocs.forEach(element => {
          operIdsList.push(element.itemValue as number);
        });
        const params: IOmniDocumentsRequest = {
          reportType: ConstantCommon.REPORT_TYPE,
          reportFormat: IFileFormat.HTML,
          reportParametersList: {
            operIds: operIdsList,
            fromDate: fromDate,
            toDate: toDate
          }
        }
        this.commonProv.presentLoading();
        this.omniPull.returnFilteredDocuments(params).then(result => {
          if (result && result.gridModel && result.gridModel.length > 0) {
            const grid = result.gridModel;
            const docList: IOmniDocumentsReportObject[] = [];
            grid.forEach(item => {
              docList.push({
                documentName: item.operName,
                operId: item.operChildId,
                showExports: item.hasData,
              });
            });
            this.documentListOptions.itemList = docList;
            this.documentListOptions.itemCard = this.itemCard;
          } else {
            if (filteredDocs.length == 0) {
              this.documentsComplexWidget.selectReportsError = true;
            }
            CommonUtils.presentInfoAlert(this.commonProv.translate('no_data_found_key'));
          }
        }).catch(error => this.commonProv.logger.log(error)).finally(() => {
          this.commonProv.dismissLoading();
        })

      }
    } else {
      for (const control of Object.keys(this.formGroup.controls)) {
        if (this.formGroup.controls[control].errors) {
          this.formGroup.get(control).setValue("");
          this.formGroup.controls[control].markAsTouched();
        }
      }
    }
  }
}

